<template>
    <div class="flex items-center text-black dark:text-white">
        <div>
          <span class="font-bold">図面</span>
          <select
            class="ml-2 px-2 py-1"
            :value="this.currentData"
            @change="onChangeCase"
          >
            <option value="基礎エース図面">基礎エース図面</option>
            <option value="基礎エース図面">柱組立図面</option>
          </select>
        </div>
    </div>
    <div 
        ref="wrapper"
        class="flex-grow w-full text-center" 
        style="height: 50%; width: 100% position: relative;">
            <canvas
            ref="canvas"
            class="mx-auto"
            style="background-size: 100% 100%"
            :style="{ 'background-image': `url(${getFullUrl(this.currentUrl)})`}"
            :height = "canvasSize.h"
            :width = "canvasSize.w"
            v-on:mouseup="myUp"
            v-on:mousedown="myDown"
            v-on:mousemove="myMove"
            v-on:contextmenu="myContext"
            v-on:mousewheel="onWheel"
            ></canvas>
    </div>
    <div class="w-full flex-grow overflow-x-auto overflow-y-auto">
      <table class="absolute text-sm border-collapse " >
        <thead>
          <tr class="border-b-2">
            <th>アクション</th>
            <th>加工<br>帳行<br>番号</th>
            <th>躯体<br>ページ<br>番号</th>
            <th>径</th>
            <th>材質</th>
            <th>切寸法</th>
            <th>切寸<br>合計<br>本数</th>
            <th>定尺長さ</th>
            <th>割数</th>
            <th>定尺本数</th>
            <th>定尺重量</th>
            <th>切寸重量</th>
            <th>定着板</th>
          </tr>
        </thead>
        <tbody>
        </tbody>
      </table>
    </div>
</template>

<script>
import Base from '@/mixins/Base.js'
import CanvasMixin from '@/mixins/Canvas.js'

export default{
    mixins: [Base, CanvasMixin],
    data(){
        return{
            currentData: '基礎エース図面',
            currentUrl: null
        }
    },
    components: {
    },
    computed: {
        imageUrl: function(){
            return this.currentData=='基礎エース図面' ? this.activeTask.base_url : this.activeTask.pillar_url
        }
    },
    watch: {
        mode: function (mode) {
            if (mode !== 'edit') {
                this.$store.dispatch('results/setActiveLabel', null)
                if (mode === 'display') {
                    this.$store.dispatch('mouse/setCursor', 'default')
                }
            }
            // this.$store.dispatch('canvas/setScale', this.canvasRatio.x) // reset zoom
            this.draw()
        },
        activeLabel: function () {
            console.log('label change')
            this.draw() // redraw
        },
        resized: function (resized) {
            if (resized) {
                // this.draw()
            }
        },
        activeDicom () {
            this.clearCanvas()
        },
        async frame (val) {
            // if
            this.$store.dispatch('angle/resetAngleA')
            this.$store.dispatch('angle/resetAngleC')
            this.$store.dispatch('distance/reset')
            if (!this.playingMovie) {
                this.$store.dispatch('canvas/setImageLoading', true)
                this.$store.dispatch('canvas/setImage', await this.addImageProcess(this.getFullUrl(val.image_url)))
                this.$store.dispatch('canvas/setImageLoading', false)
            }
            this.draw()
        },
        scale: function () {
            console.log('scale change')
            if (this.img !== null) {
                this.draw()
            }
        }
    },
    async mounted () {
        const cont = this.$refs.wrapper
        const contH = cont.clientHeight
        console.log(cont)
        console.log(contH)
        if (contH > 0 && !this.resized) {
            console.log('setting size', cont, contH)
            this.$store.dispatch('canvas/setSize', { h: contH, w: contH })
        }
        console.log(this.activeTask)
        this.$store.dispatch('canvas/setContext', this.$refs.canvas.getContext('2d'))
        this.$store.dispatch('canvas/setScale', this.canvasRatio.x)
        this.$store.dispatch('canvas/setImage', await this.addImageProcess(this.getFullUrl(this.activeTask.base_url)))
        console.log('image ready')
        // this.draw()
        // console.log('mounted')
        console.log(this.activeTask)
        this.currentUrl = this.activeTask.base_url
    },
    methods: {
        async onChangeCase(){
            console.log('onChangeCase')
            console.log(this.currentData)
            if (this.currentData=='基礎エース図面'){
                this.currentUrl = this.activeTask.base_url
            }else{
                this.currentUrl = this.activeTask.pillar_url
            }
            this.$store.dispatch('canvas/setImage', await this.addImageProcess(this.getFullUrl(this.currentUrl)))
        },
        // onWheel: function ($event) {
        //     // if (this.mode === 'display') return
        //     const delta = $event.wheelDelta
        //     if (delta > 0) {
        //         if (this.scale > 4) return
        //         console.log('zoom')
        //         this.$store.dispatch('canvas/scaleUp')
        //     } else {
        //         if (this.scale < 1) return
        //         console.log(this.scale)
        //         this.$store.dispatch('canvas/scaleDown')
        //     }
        // }
    }
}
</script>
