export default {
  data () {
    return {
      LINE_WIDTH: 1,
      DOT_DIA: 3,
      GRAB_DIA: 10,
      LINECOLORS: {
        vessel: 'rgb(0, 255, 255, 0.5)',
        lumen: 'rgb(255, 0, 0, 0.5)',
        stent: 'rgb(128, 0, 128, 0.5)'
      }
    }
  },
  methods: {
    drawRect: function (centroid) {
      const w = 2
      const h = 2
      this.context.fillRect(centroid.x - w / 2, centroid.y - h / 2, w, h)
    },
    drawCircle: function (centerX, centerY, forStrut) {
      const D = forStrut ? 8 : this.DOT_DIA
      const dia = D / this.scale * this.canvasRatio.x
      this.context.beginPath()
      this.context.arc(centerX, centerY, dia / 2, 0, 2 * Math.PI)
      this.context.closePath()
      this.context.stroke()
      this.drawRect({ x: centerX, y: centerY })
    },
    drawCurve: function (ptsa) {
      this.context.beginPath()
      this.drawLines(ptsa)
      // this.context.stroke()
    },
    drawLines: function (pts) {
      this.context.moveTo(pts[0], pts[1])
      for (let i = 2; i < pts.length - 1; i += 2) {
        this.context.lineTo(pts[i], pts[i + 1])
      }
      this.context.stroke()
    },
    drawAngle: function (center, pt1, pt2, letter) {
      this.context.beginPath()
      if (pt1) {
        this.context.moveTo(pt1.x, pt1.y)
        this.context.lineTo(center.x, center.y)
      }
      if (pt2) {
        this.context.moveTo(pt2.x, pt2.y)
        this.context.lineTo(center.x, center.y)
      }
      if (pt1 && pt2) {
        let ang1 = Math.atan2(pt1.y - center.y, pt1.x - center.x)
        let ang2 = Math.atan2(pt2.y - center.y, pt2.x - center.x)
        if (ang1 < 0) ang1 += 2 * Math.PI
        if (ang2 < 0) ang2 += 2 * Math.PI
        const arcDia = 5 / this.scale * this.canvasRatio.x
        if ((ang1 < ang2 && ang2 - ang1 <= Math.PI) || (ang1 >= ang2 && ang1 - ang2 > Math.PI)) {
          this.context.arc(center.x, center.y, arcDia, ang1, ang2)
        } else {
          this.context.arc(center.x, center.y, arcDia, ang2, ang1)
        }
        let absAng = Math.abs(ang1 - ang2)
        if (absAng > Math.PI) absAng = 2 * Math.PI - absAng
        this.$store.dispatch(`angle/setAngle${letter}Deg`, parseInt(absAng * 180 / Math.PI))
      }
      this.context.stroke()
    },
    getFlatCoords: function (coords) {
      var ret = []
      coords.forEach(elem => {
        ret.push(elem.x)
        ret.push(elem.y)
      })
      return ret
    },
    addImageProcess: function (src) {
      return new Promise((resolve, reject) => {
        const img = new Image()
        img.onload = () => resolve(img)
        img.onerror = reject
        img.src = src
      })
    },
    scaleCanvas () {
      const offset = (this.scale - this.canvasRatio.x) * this.IMAGE_PIX_SIZE_X / 2
      this.context.translate(-offset, -offset)
      this.context.scale(this.scale, this.scale)
    },
    unscaleCanvas () {
      const offset = (this.scale - this.canvasRatio.x) * this.IMAGE_PIX_SIZE_X / 2
      this.context.scale(1 / this.scale, 1 / this.scale)
      this.context.translate(offset, offset)
      this.context.setTransform(1, 0, 0, 1, 0, 0)
    },
    draw: async function () {
      this.clearCanvas(this.context)
      this.context.lineWidth = this.LINE_WIDTH / this.scale * this.canvasRatio.x
      if (this.mode === 'clean') {
        this.scaleCanvas()
        this.context.drawImage(this.img, 0, 0)
        this.unscaleCanvas()
      } else if (this.mode === 'display') {
        this.scaleCanvas()
        if (!this.playingMovie) {
          if (this.img) this.context.drawImage(this.img, 0, 0)
        }
        for (const { label, shape_type, coords } of this.currentResults) {
          this.context.fillStyle = this.LABELCOLORS[label]
          this.context.strokeStyle = this.LABELCOLORS[label]

          if (shape_type === 'point') { // strut
            console.log('strutaaaa')
            coords.map(c => {
              this.drawCircle(c.x, c.y, true)
            })
            continue
          }
          // polygon
          const curvePoints = this.getCurvePoints(this.getFlatCoords(coords), 0.5)
          this.drawCurve(curvePoints)
          const centroid = this.getCentroid(this.objectifyPoints(curvePoints))
          this.drawRect(centroid)
        }
        this.unscaleCanvas()
      } else if (this.mode === 'edit') {
        this.scaleCanvas()
        this.context.drawImage(this.img, 0, 0)

        if (this.activeResult) {
          this.context.fillStyle = this.LABELCOLORS[this.activeLabel]
          this.context.strokeStyle = this.LABELCOLORS[this.activeLabel]

          const { shape_type, coords } = this.activeResult
          coords.forEach((coord) => {
            this.drawCircle(coord.x, coord.y, shape_type === 'point')
          })
          if (shape_type === 'polygon') {
            const curvePoints = this.getCurvePoints(this.getFlatCoords(this.activeResult.coords), 0.5, this.activeResult.closed)
            this.drawCurve(curvePoints)
          }
        }
        this.unscaleCanvas()
      } else if (this.mode === 'angleA') {
        // do something
        this.context.fillStyle = 'white'
        this.context.strokeStyle = 'white'
        this.scaleCanvas()
        this.context.drawImage(this.img, 0, 0)
        const center = this.getTranslatePos({ x: this.context.canvas.getBoundingClientRect().width / 2, y: this.context.canvas.getBoundingClientRect().height / 2 })
        this.drawAngle(center, this.angleA.from, this.angleA.to, 'A')
        if (this.angleA.from && this.angleA.to) {
          const fontSize = parseInt(7 / this.scale * this.canvasRatio.x)

          this.context.font = `${fontSize}px sans-sarif`
          this.context.fillText(`${this.angleA.deg} degree`, center.x, center.y + 10)
        }
        this.unscaleCanvas()
      } else if (this.mode === 'angleC') {
        // do something
        this.context.fillStyle = 'white'
        this.context.strokeStyle = 'white'
        this.scaleCanvas()
        this.context.drawImage(this.img, 0, 0)
        const center = this.getTranslatePos({ x: this.context.canvas.getBoundingClientRect().width / 2, y: this.context.canvas.getBoundingClientRect().height / 2 })

        // this.drawCircle(center.x, center.y)
        this.drawAngle(center, this.angleC.from, this.angleC.to, 'C')
        if (this.angleC.from && this.angleC.to) {
          const fontSize = parseInt(7 / this.scale * this.canvasRatio.x)

          this.context.font = `${fontSize}px sans-sarif`
          this.context.fillText(`${this.angleC.deg} degree`, center.x, center.y + 10)
        }
        this.unscaleCanvas()
      } else if (this.mode === 'distance') {
        this.scaleCanvas()
        this.context.drawImage(this.img, 0, 0)

        if (this.distanceFrom && this.distanceTo) {
          this.context.fillStyle = 'white'
          this.context.strokeStyle = 'white'
          const midX = (this.distanceTo.x + this.distanceFrom.x) / 2
          const midY = (this.distanceTo.y + this.distanceFrom.y) / 2
          this.context.beginPath()
          this.context.moveTo(this.distanceFrom.x, this.distanceFrom.y)
          this.context.lineTo(this.distanceTo.x, this.distanceTo.y)
          this.context.stroke()
          const fontSize = parseInt(10 / this.scale * this.canvasRatio.x)
          this.context.font = `${fontSize}px sans-sarif`
          this.context.fillText(`${this.distanceMM} mm`, midX, midY)
        }
        this.unscaleCanvas()
      }
    },
    clearCanvas: function () {
      this.context.clearRect(0, 0, this.context.canvas.getBoundingClientRect().width, this.context.canvas.getBoundingClientRect().height)
    },
    getAngleIn360: function (a, b) {
      let rad = Math.atan2(a.x - b.x, a.y - b.y)
      if (rad < 0) {
        rad += 2 * Math.PI
      }
      const deg = rad * 180 / Math.PI
      return deg
    },
    sortPolygon: function (points, centroid) {
      // get centroid
      points.sort((a, b) => {
        const a1 = this.getAngleIn360(a, centroid)
        const a2 = this.getAngleIn360(b, centroid)
        const ret = parseInt(a2 - a1)
        return ret
      })
    },
    getCurvePoints: function (pts, tension, isClosed = true) {
      // use input value if provided, or use a default value
      tension = (typeof tension !== 'undefined') ? tension : 0.5
      // const isClosed = true
      const numOfSegments = 20

      var _pts = []
      var res = [] // clone array
      var x, y // our x,y coords
      var t1x, t2x, t1y, t2y // tension vectors
      var c1, c2, c3, c4 // cardinal points
      var st, t, i // steps based on num. of segments
      // clone array so we don't change the original
      //
      _pts = pts.slice(0)

      // The algorithm require a previous and next point to the actual point array.
      // Check if we will draw closed or open curve.
      // If closed, copy end points to beginning and first points to end
      // If open, duplicate first points to befinning, end points to end
      if (isClosed) {
        _pts.unshift(pts[pts.length - 1])
        _pts.unshift(pts[pts.length - 2])
        _pts.unshift(pts[pts.length - 1])
        _pts.unshift(pts[pts.length - 2])
        _pts.push(pts[0])
        _pts.push(pts[1])
      } else {
        _pts.unshift(pts[1]) // copy 1. point and insert at beginning
        _pts.unshift(pts[0])
        _pts.push(pts[pts.length - 2]) // copy last point and append
        _pts.push(pts[pts.length - 1])
      }

      // ok, lets start..

      // 1. loop goes through point array
      // 2. loop goes through each segment between the 2 pts + 1e point before and after
      for (i = 2; i < (_pts.length - 4); i += 2) {
        for (t = 0; t <= numOfSegments; t++) {
          // calc tension vectors
          t1x = (_pts[i + 2] - _pts[i - 2]) * tension
          t2x = (_pts[i + 4] - _pts[i]) * tension

          t1y = (_pts[i + 3] - _pts[i - 1]) * tension
          t2y = (_pts[i + 5] - _pts[i + 1]) * tension

          // calc step
          st = t / numOfSegments

          // calc cardinals
          c1 = 2 * Math.pow(st, 3) - 3 * Math.pow(st, 2) + 1
          c2 = -(2 * Math.pow(st, 3)) + 3 * Math.pow(st, 2)
          c3 = Math.pow(st, 3) - 2 * Math.pow(st, 2) + st
          c4 = Math.pow(st, 3) - Math.pow(st, 2)
          // calc x and y cords with common control vectors
          x = c1 * _pts[i] + c2 * _pts[i + 2] + c3 * t1x + c4 * t2x
          y = c1 * _pts[i + 1] + c2 * _pts[i + 3] + c3 * t1y + c4 * t2y

          // store points in array
          res.push(x)
          res.push(y)
        }
      }
      return res
    },
    getDistance: function (p1, p2) {
      return Math.sqrt((p1.x - p2.x) ** 2 + (p1.y - p2.y) ** 2)
    },
    isGrabbable: function (p1, p2) {
      const distance = this.getDistance(p1, p2)
      const dia = this.GRAB_DIA / 2 / this.scale * this.canvasRatio.x
      return distance < dia
    },
    myDown: async function (e) {
      // tell the browser we're handling this mouse event
      if (e.button !== 0) return
      e.preventDefault()
      e.stopPropagation()
      // get the current mouse position
      var mx = parseInt(e.clientX - this.context.canvas.getBoundingClientRect().left)
      var my = parseInt(e.clientY - this.context.canvas.getBoundingClientRect().top)
      const { x, y } = this.getTranslatePos({ x: mx, y: my })
      this.$store.dispatch('mouse/setDragging', false)
      if (this.mode === 'edit') {
        if (!this.activeResult) {
          this.$store.dispatch('results/addEmptyData')
        }
        const { coords } = this.activeResult
        coords.forEach(coord => {
          if (this.isGrabbable(coord, { x: x, y: y })) {
            if (!this.dragging) {
              this.$store.dispatch('mouse/setDragging', true)
              this.$store.dispatch('mouse/setCursor', 'wait')
              this.$store.dispatch('mouse/setStartPosition', { x: x, y: y })
              coord.isDragging = true
            }
          }
        })
        // click to add point
        if (!this.dragging) {
          await this.$store.dispatch('results/addCoord', { x: x, y: y })
          await this.$store.dispatch('results/update')
          this.draw()
          console.log('draw')
        }
      } else if (this.mode === 'angleA' || this.mode === 'angleC') {
        const modeCap = this.mode.charAt(0).toUpperCase() + this.mode.slice(1)
        const angleObj = this[this.mode]
        if (angleObj.from) {
          if (this.isGrabbable(angleObj.from, { x: x, y: y })) {
            this.$store.dispatch('mouse/setDragging', true)
            this.$store.dispatch('mouse/setCursor', 'grabbing')
            this.$store.dispatch('mouse/setStartPosition', { x: x, y: y })
            angleObj.from.isDragging = true
            return
          }
        } else {
          this.$store.dispatch(`angle/set${modeCap}From`, { x: x, y: y })
          return
        }
        if (angleObj.to) {
          if (!this.dragging) {
            if (this.isGrabbable(angleObj.to, { x: x, y: y })) {
              this.$store.dispatch('mouse/setDragging', true)
              this.$store.dispatch('mouse/setCursor', 'grabbing')
              this.$store.dispatch('mouse/setStartPosition', { x: x, y: y })
              angleObj.to.isDragging = true
            }
          }
        } else {
          this.$store.dispatch(`angle/set${modeCap}To`, { x: x, y: y })
        }
      } else if (this.mode === 'distance') {
        if (this.distanceFrom) {
          if (this.isGrabbable(this.distanceFrom, { x: x, y: y })) {
            this.$store.dispatch('mouse/setDragging', true)
            this.$store.dispatch('mouse/setCursor', 'grabbing')
            this.$store.dispatch('mouse/setStartPosition', { x: x, y: y })
            this.distanceFrom.isDragging = true
          }
        } else {
          // set distance from
          this.$store.dispatch('distance/setFrom', { x: x, y: y })
          return
        }
        if (this.distanceTo) {
          if (this.isGrabbable(this.distanceTo, { x: x, y: y })) {
            this.$store.dispatch('mouse/setDragging', true)
            this.$store.dispatch('mouse/setCursor', 'grabbing')
            this.$store.dispatch('mouse/setStartPosition', { x: x, y: y })
            this.distanceTo.isDragging = true
          }
        } else {
          this.$store.dispatch('distance/setTo', { x: x, y: y })
        }
      }
    },
    // handle mouseup events
    myUp: async function (e) {
      // tell the browser we're handling this mouse event
      e.preventDefault()
      e.stopPropagation()

      // clear all the dragging flags
      if (this.mode === 'edit') {
        if (this.dragging) {
          this.$store.dispatch('mouse/setDragging', false)
          this.activeResult.coords.forEach(coord => {
            coord.isDragging = false
          })
          this.$store.dispatch('mouse/setCursor', 'wait')
          await this.$store.dispatch('results/update')
        }
        this.$store.dispatch('mouse/setCursor', 'grab')
      } else if (this.mode === 'angleA' || this.mode === 'angleC') {
        // do something
        const modeCap = this.mode.charAt(0).toUpperCase() + this.mode.slice(1)
        if (this.dragging) {
          this.$store.dispatch('mouse/setDragging', false)
          this.$store.dispatch(`angle/set${modeCap}From`, { x: this[this.mode].from.x, y: this[this.mode].from.y, isDragging: false })
          this.$store.dispatch(`angle/set${modeCap}To`, { x: this[this.mode].to.x, y: this[this.mode].to.y, isDragging: false })
        }
        this.$store.dispatch('mouse/setCursor', 'grab')
        this.draw()
      } else if (this.mode === 'distance') {
        if (this.dragging) {
          this.$store.dispatch('mouse/setDragging', false)
          this.$store.dispatch('distance/setFrom', { x: this.distanceFrom.x, y: this.distanceFrom.y, isDragging: false })
          this.$store.dispatch('distance/setTo', { x: this.distanceTo.x, y: this.distanceTo.y, isDragging: false })
        }
        this.draw()
      }
    },
    getTranslatePos: function (pos) {
      const { x, y } = pos
      const drawOrigin = (this.scale - this.canvasRatio.x) * this.img.width / 2
      return { x: (drawOrigin + x) / this.scale, y: (drawOrigin + y) / this.scale }
    },
    // handle mouse moves
    myMove: function (e) {
      // console.log('before mymove')
      e.preventDefault()
      e.stopPropagation()
      // get the current mouse position
      var mx = parseInt(e.clientX - this.context.canvas.getBoundingClientRect().left)
      var my = parseInt(e.clientY - this.context.canvas.getBoundingClientRect().top)
      const { x, y } = this.getTranslatePos({ x: mx, y: my })
      let dx = x - this.startX
      let dy = y - this.startY

      if (this.mode === 'edit') {
        dx = x - this.startX
        dy = y - this.startY

        if (!this.activeResult) return
        if (this.dragging) {
          this.activeResult.coords.forEach(coord => {
            if (coord.isDragging) {
              coord.x += dx
              coord.y += dy
            }
          })
          this.$store.dispatch('mouse/setStartPosition', { x: x, y: y })
          this.draw()
        } else {
          let onDot = false
          this.activeResult.coords.forEach(coord => {
            if (this.isGrabbable(coord, { x: x, y: y })) {
              this.$store.dispatch('mouse/setCursor', 'grab')
              onDot = true
            }
          })
          if (!onDot) this.$store.dispatch('mouse/setCursor', 'crosshair')
        }
      } else if (this.mode === 'angleA' || this.mode === 'angleC') {
        const modeCap = this.mode.charAt(0).toUpperCase() + this.mode.slice(1)
        const center = this.getTranslatePos({ x: this.context.canvas.getBoundingClientRect().width / 2, y: this.context.canvas.getBoundingClientRect().height / 2 })
        if (this.dragging) {
          if (this[this.mode].from.isDragging) {
            this.$store.dispatch(`angle/set${modeCap}From`, { x: this[this.mode].from.x + dx, y: this[this.mode].from.y + dy, isDragging: true })
          } else {
            this.$store.dispatch(`angle/set${modeCap}To`, { x: this[this.mode].to.x + dx, y: this[this.mode].to.y + dy, isDragging: true })
          }
          this.draw()
          this.$store.dispatch('mouse/setStartPosition', { x: x, y: y })
        } else {
          this.$store.dispatch('mouse/setCursor', 'crosshair')
          //  on hover
          if (this[this.mode].from) {
            const dis = this.getDistance(this[this.mode].from, { x: x, y: y })
            if (dis < this.GRAB_DIA / 2) {
              this.$store.dispatch('mouse/setCursor', 'grab')
            }
          }
          if (this[this.mode].to) {
            const dis = this.getDistance(this[this.mode].to, { x: x, y: y })
            if (dis < this.GRAB_DIA / 2) {
              this.$store.dispatch('mouse/setCursor', 'grab')
            }
          }

          if (!this[this.mode].from) {
            this.clearCanvas()
            this.scaleCanvas()
            this.context.drawImage(this.img, 0, 0)
            this.drawAngle(center, { x: x, y: y }, null, this.mode.slice(-1))
            this.unscaleCanvas()
          } else if (!this[this.mode].to) {
            this.clearCanvas()
            this.scaleCanvas()
            this.context.drawImage(this.img, 0, 0)
            this.drawAngle(center, this[this.mode].from, { x: x, y: y }, this.mode.slice(-1))
            this.unscaleCanvas()
          }
        }
        // this.draw()
      } else if (this.mode === 'distance') {
        if (this.dragging) {
          if (this.distanceFrom.isDragging) {
            this.distanceFrom.x += dx
            this.distanceFrom.y += dy
          } else if (this.distanceTo.isDragging) {
            this.distanceTo.x += dx
            this.distanceTo.y += dy
          }
          this.draw()
          this.$store.dispatch('mouse/setStartPosition', { x: x, y: y })
        } else {
          this.$store.dispatch('mouse/setCursor', 'crosshair')
          if (this.distanceFrom) {
            if (this.isGrabbable(this.distanceFrom, { x: x, y: y })) {
              this.$store.dispatch('mouse/setCursor', 'grab')
            }
          }
          if (this.distanceTo) {
            if (this.isGrabbable(this.distanceTo, { x: x, y: y })) {
              this.$store.dispatch('mouse/setCursor', 'grab')
            }
          }
          if (this.distanceFrom && !this.distanceTo) {
            this.context.fillStyle = 'white'
            this.context.strokeStyle = 'white'
            this.clearCanvas()
            this.scaleCanvas()
            this.context.drawImage(this.img, 0, 0)
            this.context.beginPath()
            this.context.moveTo(this.distanceFrom.x, this.distanceFrom.y)
            this.context.lineTo(x, y)
            this.context.stroke()
            this.unscaleCanvas()
          }
        }
      }
      // console.log('after mymove')
    },
    myContext: function (e) {
      console.log('context')
      e.preventDefault()
      e.stopPropagation()
      var mx = parseInt(e.clientX - this.context.canvas.getBoundingClientRect().left)
      var my = parseInt(e.clientY - this.context.canvas.getBoundingClientRect().top)
      const { x, y } = this.getTranslatePos({ x: mx, y: my })
      if (this.mode === 'edit') {
        let done = false
        this.activeResult.coords.forEach((coord, index) => {
          if (this.isGrabbable(coord, { x: x, y: y })) {
            if (!done) {
              this.$store.dispatch('mouse/setDragging', false)
              this.$store.dispatch('results/deleteCoord', index)
              done = true
            }
          }
        })
        if (done) {
          this.$store.dispatch('results/update')
          this.draw()
        }
      } else if (this.mode === 'distance') {
        if (this.distanceFrom) {
          if (this.isGrabbable(this.distanceFrom, { x: x, y: y })) {
            this.$store.dispatch('distance/setFrom', null)
          }
        }
        if (this.distanceTo) {
          if (this.isGrabbable(this.distanceTo, { x: x, y: y })) {
            this.$store.dispatch('distance/setTo', null)
          }
        }
      }
    },
    objectifyPoints: function (flat) {
      const ret = []
      for (let i = 0; i < flat.length - 1; i += 2) {
        ret.push({ x: flat[i], y: flat[i + 1] })
      }
      return ret
    },
    calcPolygonArea: function (points) {
      const vertices = this.objectifyPoints(this.getCurvePoints(this.getFlatCoords(points)))
      var total = 0
      for (var i = 0, l = vertices.length; i < l; i++) {
        var addX = vertices[i].x
        var addY = vertices[i === vertices.length - 1 ? 0 : i + 1].y
        var subX = vertices[i === vertices.length - 1 ? 0 : i + 1].x
        var subY = vertices[i].y
        total += (addX * addY * 0.5)
        total -= (subX * subY * 0.5)
      }
      return (Math.abs(total) / this.canvasRatio.x / this.canvasRatio.y).toFixed(1)
    },
    getCentroid: function (pts) {
      if (pts.length === 0) return { x: null, y: null }
      var first = pts[0]
      var last = pts[pts.length - 1]
      if (first.x !== last.x || first.y !== last.y) pts.push(first)
      var twicearea = 0
      var x = 0
      var y = 0
      var nPts = pts.length
      var p1
      var p2
      var f
      for (var i = 0, j = nPts - 1; i < nPts; j = i++) {
        p1 = pts[i]
        p2 = pts[j]
        f = (p1.y - first.y) * (p2.x - first.x) - (p2.y - first.y) * (p1.x - first.x)
        twicearea += f
        x += (p1.x + p2.x - 2 * first.x) * f
        y += (p1.y + p2.y - 2 * first.y) * f
      }
      f = twicearea * 3
      return { x: x / f + first.x, y: y / f + first.y }
    }
  }
}
